<style media="screen">
    .origin_country .dropdown-toggle{
        border-top-left-radius:0px !important;
        border-bottom-left-radius:0px !important;
    }
    .add-product-hide-input span.msg-box{
        position: absolute;
        z-index: 999;
    }
    #add-product-from span.n-right{
        top: 3px;
        right: 0px;
    }
</style>
<template>
    <Alert :duration="2000" :alertshow.sync="alertObj.alertShow" :type="alertObj.alertType" :info="alertObj.alertInfo"></Alert>
    <div class="col-md-12 addproduct-box-html form-horizontal">
        <div class="well well-sm"></div>
        <div class="form-group">
            <label class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
                <p class="control-label text-muted">
                    {{productbasiinfo.SelectcategoryName.one}} > {{productbasiinfo.SelectcategoryName.two}} > {{productbasiinfo.SelectcategoryName.three}} <a @click="callStepsChange" href="javascript:;"></a>
                </p>
            </div>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-4">
                <input data-rule="required" name="productName" maxlength="30" v-model="productbasiinfo.product_name" type="text" class="form-control" placeholder="">
            </div>
            <span class="col-sm-4 control-label">
                <div class="text-left text-muted">{{productbasiinfo.product_name.length}}/30</div>
            </span>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-4">
                <input data-rule="required" name="sellingPoint" v-model="productbasiinfo.selling_point" type="text" class="form-control" placeholder="">
            </div>
            <span class="col-sm-4 control-label">
                <div class="text-left text-muted">{{productbasiinfo.selling_point.length}}/30</div>
            </span>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"></label>
            <div class="col-sm-3">
                <input name="productModel" v-model="productbasiinfo.product_model" type="text" class="form-control" placeholder="">
            </div>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-3 add-product-hide-input">
                <input data-rule="required" name="brandId" v-bind:value="productbasiinfo.brand_id" type="text" class="form-control hidden" placeholder="">
                <v-select :on-change="setBrandId" label="brandCname" :debounce="500" :on-search="setBrandListCache" placeholder="" :options="brandlist"></v-select>
            </div>
        </div>

        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label">()</label>
            <div class="col-sm-4 add-product-hide-input">
                <input name="designerVoList" v-bind:value="productbasiinfo.product_designer" type="text" class="form-control hidden" placeholder="">
                <v-select multiple label="designerName" :debounce="500" :on-search="setDesignersCache" :value.sync="productbasiinfo.product_designer" placeholder="" :options="designers"></v-select>
            </div>
        </div>

        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"></label>
            <div class="col-sm-4 add-product-hide-input">
                <input name="PrimaryDesigner" v-bind:value="isPrimaryDesigner" type="text" class="form-control hidden" placeholder="">
                <select @change="isPrimary($event)" class="form-control">
                    <option value="" selected></option>
                    <option v-for="item in productbasiinfo.product_designer" v-bind:value="$index">{{item.designerName}}</option>
                 </select>
            </div>
        </div>

        <!-- <Designers :index="$index" :itemlength="productbasiinfo.product_designer.length" v-for="item in productbasiinfo.product_designer" :designersid.sync="item.id" :designers="designers"></Designers> -->

        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span>/</label>
            <div class="col-sm-4">
                <div class="input-group add-product-hide-input">
                    <input data-rule="required" name="originCountryId" v-bind:value="productbasiinfo.origin_country" type="text" class="form-control hidden" placeholder="">
                    <span style="border-top-left-radius:4px; border-bottom-left-radius:4px;" class="input-group-addon"></span>
                    <v-select class="origin_country" :on-change="setOriginCountryId" label="keyValue" :debounce="500" :on-search="setOriginCountryCache" placeholder="" :options="originCountry"></v-select>
                </div>
            </div>
        </div>

        <div v-if="productbasiinfo.origin_country=='China'" class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span>/-</label>
            <div class="col-sm-4">
                <div class="input-group add-product-hide-input">
                    <input data-rule="required" name="originProvinceId" v-bind:value="productbasiinfo.origin_province" type="text" class="form-control hidden" placeholder="">
                    <span style="border-top-left-radius:4px; border-bottom-left-radius:4px;" class="input-group-addon"></span>
                    <v-select class="origin_country" :on-change="setOriginProvinceId" label="areaName" :debounce="500" placeholder="" :options="originProvince"  :on-search="searchProvince"></v-select>
                </div>
            </div>
            <div class="col-sm-4">
                <div class="input-group add-product-hide-input">
                    <input data-rule="required" name="originCityId" v-bind:value="productbasiinfo.origin_city" type="text" class="form-control hidden" placeholder="">
                    <span style="border-top-left-radius:4px; border-bottom-left-radius:4px;" class="input-group-addon"></span>
                    <v-select v-if="cityTag==0" class="origin_country" :on-change="setOriginCityId" label="areaName" :debounce="500" placeholder="" :options="originCity" :on-search="searchCity"></v-select>
                </div>
            </div>
        </div>

        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-2 add-product-hide-input">
                <input data-rule="required" name="styleId" v-bind:value="productbasiinfo.style" type="text" class="form-control hidden" placeholder="">
                <select v-model="productbasiinfo.style" class="form-control">
                    <option value="" selected></option>
                    <option v-for="item in style" v-bind:value="item.keyName">{{item.keyValue}}</option>
                 </select>
            </div>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-2">
                <div class="input-group">
                    <span class="input-group-addon"></span>
                    <input data-rule="required" name="length" v-model="productbasiinfo.long" type="number" class="form-control" placeholder="">
                    <span class="input-group-addon">cm</span>
                </div>
            </div>
            <div class="col-sm-2">
                <div class="input-group">
                    <span class="input-group-addon"></span>
                    <input data-rule="required" name="width" v-model="productbasiinfo.width" v-model="productbasiinfo.width" type="number" class="form-control" placeholder="">
                    <span class="input-group-addon">cm</span>
                </div>
            </div>
            <div class="col-sm-2">
                <div class="input-group">
                    <span class="input-group-addon"></span>
                    <input data-rule="required" name="height" v-model="productbasiinfo.height" v-model="productbasiinfo.height" type="number" class="form-control" placeholder="">
                    <span class="input-group-addon">cm</span>
                </div>
            </div>
            <span class="col-sm-4 control-label">
                <div class="text-left text-muted"></div>
            </span>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-2 add-product-hide-input">
                <input data-rule="required" name="applicablePeople" v-bind:value="productbasiinfo.applicable_people" type="text" class="form-control hidden" placeholder="">
                <select v-model="productbasiinfo.applicable_people" class="form-control">
                    <option value="" selected></option>
                    <option v-for="item in applicable_people" v-bind:value="item.keyName">{{item.keyValue}}</option>
                 </select>
            </div>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-2 add-product-hide-input">
                <input data-rule="required" name="canCustomized" v-bind:value="productbasiinfo.can_customize" type="text" class="form-control hidden" placeholder="">
                <select v-model="productbasiinfo.can_customize" class="form-control">
                    <option selected value=""></option>
                    <option v-bind:value=true></option>
                    <option v-bind:value=false></option>
                 </select>
            </div>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span>
            </label>
            <div class="col-sm-7 bg-muted">
                <label v-for="item in applicable_scene" class="checkbox-inline">
                    <input data-rule="checked[1~]" type="checkbox" name="applicableScene[]" v-model="productbasiinfo.applicable_scene_text" v-bind:value="item.keyName"> {{item.keyValue}}
                </label>
            </div>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-7 bg-muted">
                <label class="checkbox-inline" v-for="item in productbasiinfo.material_list">
                    <input data-rule="checked[1~]" name="materialList[]" v-model="productbasiinfo.material_text" type="checkbox" value="{{item.id}}"> {{item.material}}
                </label>
            </div>
        </div>
    </div>
</template>
<script type="text/javascript">
    import Select2 from '../../../../components/common/selecte2/Selecte2'
    import Designers from './Designers'
    import vSelect from '../../../../components/common/vue-select/src/index.js'
    import Alert from '../../../../components/common/alert/Alert'
    import {API_ROOT} from '../../../../config'

    export default{
        props:['productbasiinfo'],
        components:{
            Select2,
            Designers,
            vSelect,
            Alert
        },
        data(){
            return{
                brandlist:null,
                designers:null,
                originCountry:null,
                originProvince:[],
                originCity:[],
                isPrimaryDesigner:'',
                alertObj:{
                    alertType:null,
                    alertInfo:null,
                    alertShow:false,
                },
                cityTag:0,
                style:[],
                applicable_people:[],
                applicable_scene:[]
            }
        },
        methods:{
            //
            callStepsChange:function(){
                this.$dispatch('callStepsChangeFater','1');
            },

            //
            dictionarys:function(){
                let jsontext=JSON.stringify({"keyGroups":["style","applicable_people","applicable_scene"]});
                this.$http.get(API_ROOT+'admin-api-dev/v1/dictionarys',{paramJson:jsontext}).then((response) => {
                    if (response.data.resCode==0) {
                        this.$set('style',response.data.data.style);
                        this.$set('applicable_people',response.data.data.applicable_people);
                        this.$set('applicable_scene',response.data.data.applicable_scene)
                    }else {
                    }
                }, (response) => {
                    this.$set('alertObj',{alertType:'alert-danger',alertInfo:'',alertShow:true})
                });
            },

            /**
             * 
             */
            //
            setBrandListCache:function(){
                this.$http.get(API_ROOT+'/admin-api-dev/v1/brand/queryAll',{}).then((response) => {
                    if (response.data.resCode==0) {
                        this.$set('brandlist',response.data.data.brandList);
                    }else {
                    }
                }, (response) => {
                    this.$set('alertObj',{alertType:'alert-danger',alertInfo:'',alertShow:true})
                });
            },
            //id
            setBrandId(val) {
                if (!val) {
                    this.$set('productbasiinfo.brand_id','')
                    return;
                }
                this.$set('productbasiinfo.brand_id',val.id);
                $('#add-product-from').data('validator').hideMsg('input[name="brandId"]');
            },

            /**
             * 
             */
             //
             setDesignersCache:function(){
                 this.$http.get(API_ROOT+'admin-api-dev/v1/designer/queryAllDesigner',{}).then((response) => {
                     if (response.data.resCode==0) {
                         this.$set('designers',response.data.data);
                     }else {
                     }
                 }, (response) => {
                     this.$set('alertObj',{alertType:'alert-danger',alertInfo:'',alertShow:true})
                 });
             },
            //
            isPrimary:function(event){
                if (event.target.value=='') {
                    this.$set('isPrimaryDesigner','');
                    for(let i=0;i<this.productbasiinfo.product_designer.length;i++){
                        this.$set('productbasiinfo.product_designer['+i+'].primary',false);
                    }
                }else {
                    for(let i=0;i<this.productbasiinfo.product_designer.length;i++){
                        this.$set('productbasiinfo.product_designer['+i+'].primary',false);
                    }
                    this.$set('productbasiinfo.product_designer['+event.target.value+'].primary',true);
                    this.$set('isPrimaryDesigner',event.target.value);
                    $('#add-product-from').data('validator').hideMsg('input[name="PrimaryDesigner"]');
                }
            },

            /**
             * 
             */
            //
            setOriginCountryCache:function(){
                let jsontext=JSON.stringify({"keyGroups":["country"]});
                this.$http.get(API_ROOT+'admin-api-dev/v1/dictionarys',{paramJson:jsontext}).then((response) => {
                    if (response.data.resCode==0) {
                        this.$set('originCountry',response.data.data.country);
                    }else {
                    }
                }, (response) => {
                    this.$set('alertObj',{alertType:'alert-danger',alertInfo:'',alertShow:true})
                });
            },
            //id
            setOriginCountryId(val) {
                if (!val) {
                    this.$set('productbasiinfo.origin_country','')
                    return;
                }
                if(val.keyName!='China'){
                    this.$set('productbasiinfo.origin_province','');
                    this.$set('productbasiinfo.origin_city','')
                }
                if (val.keyName=='China') {
                    this.searchProvince();
                }
                this.$set('productbasiinfo.origin_country',val.keyName);
                $('#add-product-from').data('validator').hideMsg('input[name="originCountryId"]');
            },
            //
 			searchProvince(){
                let jsontext=JSON.stringify({"areaId":0});
                this.$http.get(API_ROOT+'admin-api-dev/v1/area/subarea',{paramJson:jsontext}).then((response) => {
                    // success callback
                    if (response.data.resCode==0) {
                        this.$set('originProvince',response.data.data.areaList);
                    }else {
                    }
                }, (response) => {
                    // error callback
                    this.$set('alertObj',{alertType:'alert-danger',alertInfo:'',alertShow:true})
                });
            },
            //
            setOriginProvinceId(val){
                if (!val) {
                    this.$set('productbasiinfo.origin_province','')
                    return;
                }
                this.$set('productbasiinfo.origin_province',val.id)
                this.$set('productbasiinfo.origin_city','')
                this.searchCity(val.id)
                this.$set('cityTag',1);
                setTimeout(() => {
                    this.$set('cityTag',0);
                }, 10);
                $('#add-product-from').data('validator').hideMsg('input[name="originProvinceId"]');
            },
            //
 			searchCity(id){
                let jsontext=JSON.stringify({"areaId":id});
                this.$http.get(API_ROOT+'admin-api-dev/v1/area/subarea',{paramJson:jsontext}).then((response) => {
                    // success callback
                    if (response.data.resCode==0) {
                        this.$set('originCity',response.data.data.areaList);
                    }else {
                    }
                }, (response) => {
                    // error callback
                    this.$set('alertObj',{alertType:'alert-danger',alertInfo:'',alertShow:true})
                });
            },
            //
            setOriginCityId(val){
                if (!val) {
                    this.$set('productbasiinfo.origin_city','')
                    return;
                }
                this.$set('productbasiinfo.origin_city',val.id)
                $('#add-product-from').data('validator').hideMsg('input[name="originCityId"]');
            },
        },
        ready(){

            /**
             * 
             */

            // 
            this.setBrandListCache();

            // 
            this.setDesignersCache();

            // 
            this.setOriginCountryCache();

            // 
            this.dictionarys();

        }
    }
</script>



